.img-double {
  position: relative;
  z-index: 2;
  max-width: 400px;
  margin: 0 auto;
  padding-bottom: 40px; }
  .img-double:hover > .img:first-child {
    transform: translate(40px, 40px); }
    [dir="rtl"] .img-double:hover > .img:first-child {
      transform: translate(-40px, 40px); }
  .img-double:hover > .img:first-child:before {
    opacity: 0.92; }
  .img-double:hover > .img:last-child:before {
    opacity: 0; }
  .img-double:hover > .img:last-child {
    z-index: 3;
    transform: translate(-40px, -40px); }
    [dir="rtl"] .img-double:hover > .img:last-child {
      transform: translate(40px, -40px); }
  .img-double > .img {
    position: relative;
    z-index: 2;
    width: calc(100% - 40px);
    max-width: 360px;
    height: calc(100% - 40px);
    max-height: 360px;
    transition: all .4s ease; }
    .img-double > .img img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .img-double > .img:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(to right, var(--brk-base-3), var(--secondary));
      transition: all .4s ease;
      opacity: 0.92;
      z-index: 2; }
    .img-double > .img:first-child:before {
      opacity: 0; }
    .img-double > .img:last-child {
      z-index: 1;
      position: absolute;
      bottom: 0;
      right: 0; }
